@use "../../selectors" as vars;
@use "../../shared/css-vars" as css-vars;
@use "../../shared/functions" as fn;
@use "../../shared/mixins" as mx;

$main-table-color: css-vars.get-color-var("light-gray");
$table-border: fn.to-rem(2px) solid $main-table-color;
$table-cell-padding: fn.to-rem(16px) fn.to-rem(20px);

#{vars.$table-selector} {
  width: 100%;
  max-width: 100%;

  border-collapse: collapse;
  
  // Head Cells
  #{vars.$table-head-cell-selector} {
    border: $table-border;
    background-color: $main-table-color;
    padding: $table-cell-padding;
    text-align: left;
  }

  // Regular Cells
  #{vars.$table-cell-selector} {
    padding: fn.to-rem(16px) fn.to-rem(20px);
    border: $table-border;
  }
}
